import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'

export default {
  name: 'LivePlayer',
  components: {
    videoPlayer
  },
  props: {
    poster: {
      type: String,
      default: ''
    },
    link: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      playerOptions: {
        aspectRatio: '16:9',
        muted: false,
        preload: 'metadata',
        language: 'zh-CN',
        nativeControlsForTouch: false,
        sources: [{
          type: 'video/mp4',
          src: this.link
        }],
        poster: this.poster,
        notSupportedMessage: '当前视频无法播放哦～'
        // children: {
        //   ControlBar: {
        //     CurrentTimeDisplay: true,
        //     RemainingTimeDisplay: true
        //   }
        // }
      }
    }
  },
  mounted () {
    window._player = this.player
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    }
  }
}



