
import Loading from 'vux/src/components/loading/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'
import dateFormat from 'vux/src/tools/date/format.js'

import api from '@/utils/api'
import LivePlayer from './pagelet/LivePlayer'

export default {
name: 'Live',
components: {
Loading,
LivePlayer
},
data () {
return {
liveId: '',
live: null,
loading: true
}
},
computed: {
createtime () {
return dateFormat(this.live.createtime * 1000, 'YYYY-MM-DD')
},
poster () {
return this.live.covermap
}
},
methods: {
getLiveId () {
this.liveId = querystring.parse().liveId
},
formatStatus (status) {
switch (status) {
case '3':
return '结束回看'
case '2':
return '直播中...'
case '1':
return '未开始'
default:

}
},
async fetchData () {
this.loading = true
try {
const data = await api.getLiveDetail(this.liveId)
if (data) {
this.live = Object.assign({}, data)
}
} catch (err) {
console.error(err)
}
this.loading = false
}
},
mounted () {
this.getLiveId()
this.fetchData()
}
}



